.menu {
  height: 100vh;
  background-color: $menu-background-color;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto;

  &__top {
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: $menu-background-color;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 27px;
    color: $menu-text-color;
    text-decoration: none;
    transition: text-shadow 300ms;

    &:hover {
      text-shadow: 1px 4px 16px 0 #000;
    }
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $menu-text-color;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: $menu-text-color;
    text-decoration: none;
  }
}
